#header{
    background-color: lightblue;
    width:100%;
    height:50px;
    overflow: hidden;
    text-align: center;
}

#header a {
    float: left;
    display: block;
    color: black;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}


#footer{
    clear:both;
    height: 150px;
    width: 100%;
    /* text-align: center; */
    background-color: rgb(15, 25, 29);
}

#main{
    min-height: 700px;
    float:left;
    width:100%;
}

#data-stream{
    margin-top: 20px;
    font-size:larger;
}

footer {
    display: block;
    position:fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    /* background-color: red; */
    color: white;
    text-align: center;
}

#footer-id{
    font-family:'Courier New', Courier, monospace;
    color: white;
    text-decoration: none;
    top: 200px;
}

#footer-id-a{
    top: 200px;
