}

#analog-clock{
    margin-top: 50px;
    /* align-items: center; */
    /* align-content: center; */
}

#timestamp-epoch{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size:40px;
}

#title-epoch{
    font-family:Arial, Helvetica, sans-serif;
